








































































export default {
	data() {
		return {
			index: -1,
			picker: ['男', '女'],
			
			index1: -1,
			picker1: ['50','49', '48', '47','46', '45', '44','43', '42', '41','40', '39', '38','37', '36', '35','34', '33', '32', '31','30', '29', '28','27', '26', '25','24', '23', '22', '21','20', '19', '18','17', '16'],
			
			picker2: ['否','是'],
			index2: -1,
			picker3: ['硕士','博士', '本科', '专科','高中', '初中'],
			index3: -1,
			multiIndex: [0, 0, 0],
			time: '12:01',
			date: '2018-12-25',
			name:'',
			work:'',
			phone:'',
			id:'',
			item:[]
			
		};
	},
	onLoad:function(e){
		// console.log();
		var item=JSON.parse(e.con);
		var _this=this;
		this.item=item;
		console.log(item)
		if(item.gender==1){
			this.index=0
		}
		if(item.gender==2){
			this.index=1
		}
		this.index2=item.isSoldierPriority;
		
		
		this.name =item.name;
		this.phone =item.phone;
		this.work =item.workYear;
		this.id =item.id;
		var year =item.age;
		var education=item.education;
		for(var i=0;i<_this.picker1.length;i++){
			if(_this.picker1[i]==year){
				_this.index1=i;
				break;
			}
		}
		for(var i=0;i<_this.picker3.length;i++){
			if(_this.picker3[i]==education){
				_this.index3=i;
				break;
			}
		}
	},
	methods: {
		PickerChange(e) {
			this.index = e.detail.value
		},
		PickerChange1(e) {
			this.index1 = e.detail.value
		},
		PickerChange2(e) {
			this.index2 = e.detail.value
		},
		PickerChange3(e) {
			this.index3 = e.detail.value
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		add4:function(){
			
				if(this.name < 1){uni.showToast({title:'请输入姓名', icon:"none"}); return ;}
				if(this.work < 1){uni.showToast({title:'请输入工作年限', icon:"none"}); return ;}
				if(this.phone.length!=11){uni.showToast({title:'请输入联系电话', icon:"none"}); return ;}	
				if(this.index == -1){uni.showToast({title:'请选择性别', icon:"none"}); return ;}
				if(this.index1 == -1){uni.showToast({title:'请选择年龄', icon:"none"}); return ;}
				if(this.index3 == -1){uni.showToast({title:'请选择学历', icon:"none"}); return ;}
				if(this.index2 == -1){uni.showToast({title:'请选择是否军人', icon:"none"}); return ;}
				var _self=this;
				uni.showLoading({title:"正在提交"});
				console.log(_self.imglist);
				uni.request({
					url: _self.apiServer + '/api/v1.Resume/edit',
					method: 'POST',
					header: {'content-type' : "application/x-www-form-urlencoded"},
					data:{
						"resumeId":parseInt(_self.id),
						"name":_self.name,
						
						"phone":_self.phone,
						"gender":parseInt(_self.index)+1,
						"age":parseInt(_self.picker1[_self.index1]),
						"workYear":parseInt(_self.work),
						"education":_self.picker3[_self.index3],
						"isSoldierPriority":_self.index2,
						
						"skills":_self.item.skills,
						"selfEvaluation":_self.item.selfEvaluation,
						
						"militaryTime":_self.item.militaryTime,
						"attendedTime":_self.item.attendedTime,
						"corps":_self.item.corps,
						"exPosition":_self.item.exPosition,
						"nature":_self.item.nature,
						"exCity":_self.item.exCity,
						"salary":_self.item.salary,
						"curStatus":_self.item.curStatus,
						"arrivalTime":_self.item.arrivalTime,
						
						
						accessToken:uni.getStorageSync('utoken'),
						"id_token":uni.getStorageSync('token'),
						
					},
					success:function(res){
						console.log(res);
						if(res.data.errorCode == '0'){
							uni.showToast({title:"更新成功", icon:"none"});
							_self.imglist = [];
							
							setTimeout(function(){
								uni.switchTab({
									url:"jianli"
								})
							}, 1000);
						}else{
							uni.showToast({title:res.data.msg, icon:"none"});
						}
					}
				});
			
			
		}
	}
}
